import CardPokemon from "./CardPokemon";
// eslint-disable-next-line no-unused-vars
import React, { useEffect, useState } from "react";

// eslint-disable-next-line react/prop-types
const CardList = ({pokemons, offsets}) =>{
  const test = false;
    // eslint-disable-next-line react/prop-types
    const [pokemonDetails, setPokemonDetails] = useState([])
  useEffect(() => {
    const fetchPokemonDetails = async () => {
      const detailsPromises = pokemons.map((pokemon) =>
        fetch(pokemon.url).then((response) => response.json())
      );

      try {
        const details = await Promise.all(detailsPromises);
        setPokemonDetails(details);
      } catch (error) {
        console.error(error);
      }
    }

    fetchPokemonDetails(); 
  }, [pokemons]);



    const pokemonsWithSprites = pokemons.map((pokemon,i) =>{
        return {
          ...pokemon,
          sprite: `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${offsets + i +1}.png`
        }
        

      })

      console.log(pokemonsWithSprites)
    return <>
        {}
        <div className="font-montserrat p-10 flex flex-col justify-center items-center xl:grid xl:grid-cols-5 gap-16">
        {
        pokemonsWithSprites.map((pokemon, index) => (
        <div key={index}>
            
           <CardPokemon url={pokemon.url} id={parseInt(pokemon.url.match(/\/(\d+)\/$/)[1])} name={pokemon.name} sprite={pokemon.sprite}></CardPokemon>
        </div>
      ))}
                
        </div>
    </>

}

export default CardList;