import React from "react";
import {
  Button,
  Dialog,
  DialogHeader,
  DialogBody,
  DialogFooter,
} from "@material-tailwind/react";
import Gallery from "./Gallery";
 
const DialogDefault = ({isOpen, handleOpen, handleClose, name, image, id}) => {
 
  return (
    <>
      <Dialog  size="xs" open={isOpen} handler={handleOpen}>
        <DialogHeader className="flex justify-center items-center">{name}</DialogHeader>
        <DialogBody divider>
            <Gallery id= {id} image={image}></Gallery>
        </DialogBody>
        <DialogFooter className="flex justify-start items-start">
          <Button
            variant="text"
            color="red"
            onClick={handleClose}
            className="w-6/12"
          >
            <span>Cancel</span>
          </Button>
          <Button variant="gradient" onClick={handleOpen} className="w-6/12 bg-amber-600">
            <span>Confirm</span>
          </Button>
        </DialogFooter>
      </Dialog>
    </>
  );
}
export default DialogDefault