import React, { useEffect, useState } from "react";
import {
  Carousel,
  Typography,
  Button,
  IconButton,
} from "@material-tailwind/react";
const Gallery = ({id}) => {
  console.log("id",id);
const backImage = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/back/${id}.png`
const frontImage = `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${id}.png`
  return (
    <div className="">
<div className="bg-primary rounded-xl">
      <Carousel
        className="rounded-xl"
        navigation={({ setActiveIndex, activeIndex, length }) => (
          <div className="absolute bottom-4 left-2/4 z-50 flex -translate-x-2/4 gap-2">
            {new Array(length).fill("").map((_, i) => (
              <span
                key={i}
                className={`block h-1 cursor-pointer rounded-2xl transition-all content-[''] ${
                  activeIndex === i ? "w-8 bg-white" : "w-4 bg-white/50"
                }`}
                onClick={() => setActiveIndex(i)}
              />
            ))}
          </div>
        )}
      >
        <div className="relative h-full w-full object-fill flex justify-center items-center">
        <img
          src={frontImage}
          alt="image 1"
          className="h-full w-44"
        />
        </div>
        <div className="relative h-full w-full object-fill flex justify-center items-center">
        <img
          src={backImage}
          alt="image 1"
          className="h-full w-44"
        />
        </div>

      </Carousel>
      </div>
    </div>
  );
};

export default Gallery;
